.home {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--black) url("../img/bg.jpg") center/cover no-repeat;
  color: var(--white);

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__text {    
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 10;
    margin-bottom: 80px;
  }

  &__text-title {
    text-align: center;
    font-size: 72px;
    font-weight: bold;
    margin-bottom: 32px;    
  }

  &__text-title-dao {
    position: relative;

    &::before {
      content: url("../img/daologo.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      top: 20px;
      left: -10px;
    }
  }

  &__text-subtitle {
    text-align: center;
    max-width: 700px;
    font-size: 24px;
    color: var(--grey);
    line-height: 1.5;
  }

  &__btn {
    color: var(--black);
    padding: 20px 40px;
    background: linear-gradient(180deg, #FFD600 0%, #FFB800 100%);
    border-radius: 16px;
    transition: all .5s ease;
    font-weight: bold;
    font-size: 20px;
    align-self: center;
    &:hover {
      border-radius: 50px;
    }
  }
}


@include px1024 {
  .home {

    &__inner {}

    &__text {
      margin-bottom: 52px;
    }

    &__text-title {
      font-size: 52px;
    }

    &__text-title-dao {}

    &__text-subtitle {
      font-size: 20px;
    }

    &__btn {}
  }
}


@include px533 {
  .home {

    &__inner {}

    &__text {
      margin-bottom: 32px;
    }

    &__text-title {
      font-size: 36px;
      margin-bottom: 20px;
    }

    &__text-title-dao {

      &::before {
        left: -20px;
        top: 15px;
        transform: scale(0.7);
      }
    }

    &__text-subtitle {
      font-size: 16px;
    }

    &__btn {}
  }
}