.header {
  background: var(--black);
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;


  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
  }

  &__logo-wrap {
    display: flex;
    align-items: center;
    font-size: 28px;
    color: #ffffff;
    font-weight: bold;
    text-transform: capitalize;
    letter-spacing: 0.5px;
  }

  &__logo-wrap img{
    margin-right: 10px;
  }

  &__logo {
    max-width: 50px;
    max-height: 50px;
  }

  &__burger {
    cursor: pointer;
    display: none;
    border-radius: 50%;
    
  }

  &__nav {}

  &__ul {
    display: grid;
    grid-auto-flow: column;
    gap: 20px;
  }

  &__li {}

  &__a {
    color: var(--grey);
    white-space: nowrap;
    padding: 15px;
    font-size: 20px;
    transition: all .3s ease;

    &:hover {
      color: var(--white);
    }
  }
  &__btn {
    background: #262E37;
    color: var(--white);
    padding: 15px 25px;
    border-radius: 4px;
    transition: all .3s ease;
    font-weight: bold;
    &:hover {
      background: var(--grey);
      color: var(--black);
    }
  }

  &__btn-img {
    position: relative;
    top: 3px;
  }
}

.container {}



.line {
  fill: none;
  stroke: var(--white);
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
              stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
  stroke: var(--white);
  transition: all 0.6s ease;
}

.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
  stroke: var(--white);
  transition: all 0.6s ease;
}

.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
  stroke: var(--white);
  transition: all 0.6s ease;
}



@include px800 {
  .header {

    &__inner {}

    &__logo-wrap {}

    &__logo {}

    &__burger {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 101;
    }

    &__nav {
      background: var(--black);
      transition: all .5s ease;
      position: absolute;
      top: 0%;
      right: -100%;
      width: 100%;
      height: 100vh;
      transition: all .5s ease;
      overflow: auto;
    }

    &__ul {      
      margin-top: 50px;
      display: grid;
      grid-auto-flow: row;
      justify-content: center;
      gap: 40px;
    }

    &__li {}

    &__a {
      font-size: 26px;
    }

    
  }

  .container {}

  .header__nav-active {
    position: absolute;
    top: 0%;
    right: 0%;
    transition: all .5s ease;
  }
}

